.modal-container {
  background: var(--color-bg-popup-shade);
  justify-content: center;
  align-items: center;
  transition-duration: .2s;
  animation: .25s linear fade-in-modal;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal-content {
  background: var(--color-bg-modal);
  border-radius: .4rem;
  flex-direction: column;
  justify-content: space-between;
  min-width: 320px;
  min-height: 320px;
  animation: .1s linear popup-modal;
  display: flex;
  overflow: hidden;
}

.modal-header {
  box-shadow: var(--shadow-lg);
  align-items: center;
  min-height: 2rem;
  padding: .5rem 1rem;
  font-size: 2rem;
  display: flex;
  position: relative;
}

.modal-header .modal-close {
  color: #fff;
  text-shadow: var(--shadow-text);
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1em;
  transition-duration: .15s;
  position: absolute;
  right: 1rem;
}

.modal-header .modal-close:hover {
  text-shadow: unset;
}

.modal-body {
  flex-grow: 1;
  max-height: 80vh;
  padding: .5rem 1rem;
  overflow: auto;
}

.modal-footer {
  box-shadow: var(--shadow-lg);
  padding: .3rem 1rem;
  font-size: 1.5em;
}

.modal-hide {
  opacity: 0;
}

@keyframes popup-modal {
  0% {
    opacity: 0;
    margin-left: 100px;
    scale: .4;
  }

  100% {
    opacity: 1;
    margin-left: 0;
    scale: 1;
  }
}

@keyframes fade-in-modal {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

